import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import CollectionBox from "../components/collection-box"

const COLLECTIONS = [
  {
    name: 'qm',
    description: "Quantum Mechanics",
    img_src: "/images/qm.jpg"
  },
  {
    name: 'ml',
    description: "Machine Learning",
    img_src: "/images/ml.jpg"
  },
  {
    name: 'distributed-system',
    description: "Distributed System",
    img_src: "/images/ds.jpg"
  },
  {
    name: 'abstract-algebra',
    description: "Abstract Algebra",
    img_src: "/images/aa.png"
  },
];

const Index = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title
  const collections = 
    COLLECTIONS.map( item => <CollectionBox {...item}/>)
      
  return (
    <Layout location={location} title={siteTitle} isIndex>
      <SEO title="NowhereSayings" />
      <div
        style={{
          width: "100%",
          textAlign: "center",
          marginTop: "-2.5rem",
          marginBottom: "3rem",
        }}
      >
      {collections}
      </div>
    </Layout>
  )
}

export default Index

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
